/* @import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap'); */

/* base styles */
* {
    margin: 0;
    font-family: "Quicksand";
    color: #333;
}
.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom: 1px solid #f2f2f2;
    margin-bottom: 20px;
}
.navbar h1 {
    color: #f1356d;
}
.navbar .links {
    margin-left: auto;
}
.navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}
.navbar a:hover {
    color: #f1356d;
}
.content {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
}
.blog-preview {
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 3px solid #fafafa;
}
.blog-preview h1 {
    color: #f1356d;
    font-size: 20px;
}
.blog-preview p {
    color: #333;
    font-size: 15px;
}
.blog-preview .delete-btn {
    background-color: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    cursor: pointer;
    border-radius: 8px;
    &:hover {
        background-color: #d11f54;
    }
}
.blog-preview .author {
    font-size: 14px;
    font-weight: 600;
    color: #999;
    padding: 10px;
}
.title {
    margin: 20px;
}
.add-blog-btn {
    background-color: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    cursor: pointer;
    border-radius: 8px;
    &:hover {
        background-color: #d11f54;
    }
}
.loading-msg {
    font-size: 20px;
    color: #f1356d;
    padding: 20px;
}
.blog-title {
    font-size: 24px;
    color: #f1356d;
} 
.blog-author {
    font-size: 16px;
    color: #999;
} 
.blog-body {
    font-size: 16px;
    color: #333;
    max-width: 50vw;
    width: 100%;
    margin: 0 auto;
    padding: 20px;
    word-wrap: break-word;
    
    @media screen and (max-width: 768px) {
        max-width: 90vw;
    }
}
.blog-list {
    padding: 20px;
    margin-top: 50px;
}
form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    max-width: 50vw;
    margin: 0 auto;
    padding: 20px;

    @media screen and (max-width: 768px) {
        max-width: 90vw;
    }
}

.blog-preview {
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
}

.blog-body {
    min-height: 200px;
    padding: 8px;
    border: 1px solid #ddd;
    border-radius: 4px;
    font-size: 16px;
    resize: vertical;
}

.delete-btn {
    margin-top: 20px;
    width: fit-content;
    align-self: flex-start;
    background-color: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    cursor: pointer;
    border-radius: 8px;
    &:hover {
        background-color: #d11f54;
    }
}

form label {
    margin-top: 10px;
}

